<template>
	<div id="collection">
		<div id="title">
			<i @click="prev" class="el-icon-loading prev"></i>
			我得收藏
		</div>
		<div id="list">
			<div id="empty">
				<p>好看的不能让TA溜走</p>
				<p>点击<i class="el-icon-star-off"></i>收起来</p>
				<p>登录后喜欢会同步保存，永远不会消失</p>
			</div>
		</div>
	</div>
</template>

<script>
	export default{
		data(){
			return{

			}
		},
		methods:{
			prev:function(){
				this.$router.push({
					path:'/index/my'
				})
			}
		},
		created:function(){

		},
		mounted:function(){

		}

	}
</script>

<style lang="scss" scoped>
	#collection{
		#title{
			background: #2992d9;
	    padding:1rem 0;
	    font-size:1rem;
	    font-weight:bold;
	    letter-spacing:.12rem;
	    color:#fff;
			.prev{
				font-weight:bold;
				position:absolute;
				left:1rem;
				top:.2rem;
				padding:1rem;
			}
    }
    #list{
    	padding:1rem;
    	#empty{
				padding:5rem 1rem 1rem 1rem;
				p{
					margin-top: 1rem;
				}
				p:first-child{
					font-size:1rem;
				}
				p:nth-child(2){
					
					font-size:1.4rem;
					color:#df9db3;
					i{
						font-size:2rem;
						padding:0 .6rem;
					}
				}
				p:last-child{
					padding:.6rem 0 0 0;
					display:inline-block;
					border-top:1px solid #dfdfdf;
					font-size:1rem;
				}
    	}
    }
	}
	
</style>


